* {
  font-family: "Roboto", sans-serif;
  font-family: "EB Garamond", serif;
  font-family: "Roboto", sans-serif;
}
.box {
  margin-top: 10px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.widthf {
  width: 300px;
  height: 400px;
  background-color: blueviolet;
  border-radius: 20px;
}

@media (max-width: 450px) {
  .widthf {
    background-color: darkgray;
    width: 200px;
    height: 300px;
  }
}

.inputField {
  margin-top: 10px;
  margin-left: 50px;
  background-color: aliceblue;
  height: 40px;
  width: 200px;
  border: 3px solid red;
  border-radius: 20px;
  padding: 10px;
  font-size: 15px;
  font-weight: bolder;
}

@media (max-width: 450px) {
  .inputField {
    width: 150px;
    margin-left: 25px;
  }
}

.info {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
.location {
  text-shadow: 2px 2px white;
  font-size: 35px;
  margin-top: 100px;
}

@media (max-width: 450px) {
  .location {
    margin-top: 50px;
    text-shadow: 2px 2px 2px skyblue;
  }
}

.temp {
  text-shadow: 2px 2px 2px rgb(32, 196, 20);
  font-size: 30px;
  margin: 20px;
}

.temp-max {
  margin-top: 10px;
}
@media (max-width: 450px) {
  .temp-max {
    margin-left: 10px;
    color: white;
  }
}
.error {
  display: flex;
  justify-content: center;
  margin-top: 10px;
  padding: 10px;
  font-size: 20px;
  font-weight: bolder;
}
